import React from 'react';
import { Footer } from './Footer';
import { Navbar } from './Navbar';

export const DefaultLayout = ({ children, coverImage, coverText }) => {
  return (
    <>
      <header><Navbar coverImage={coverImage} coverText={coverText} /></header>
      <main>{children}</main>
      <footer><Footer /></footer>
    </>
  )
}
