import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { HOST_NAME } from "../../config";


export const FindGuide = ({ onQueryChange, handleSearch }) => {
    const [open, setOpen] = useState(false);
    const [suggestions, setSuggestions] = useState([]);
    const [searchValue, setSearchValue] = useState('');
    const navigate = useNavigate();
    const result = useRef();
    const requestTimer = useRef();

    const loadSuggestions = useCallback(async () => {
        const url = HOST_NAME + "api/guide/suggestion/";
        const params = new URLSearchParams();
        params.append("q", searchValue);
        const response = await axios.get(url, { params: params });
        if (response.status === 200) {
            setSuggestions(response.data.data);
        }
    }, [searchValue]);

    // Load initial suggestions
    useEffect(loadSuggestions, []);
    useEffect(() => onQueryChange(searchValue), [searchValue]);

    const handleChange = useCallback((e) => {
        setSearchValue(e.target.value)
        if (requestTimer.current)
            clearTimeout(requestTimer.current);

        requestTimer.current = setTimeout(() => {
            requestTimer.current = null;
            loadSuggestions();
        }, 500);
    }, [requestTimer, onQueryChange, loadSuggestions]);

    const handleSelect = useCallback((item) => {
        setSearchValue(item.text);
        setOpen(false);
        navigate(`/guide-instagram/${item.id}`);
    }, [])


    useEffect(() => {
        const closeResault = e => {
            if (open && result.current && !result.current.contains(e.target)) {
                setOpen(false)
            }
        }
        document.body.addEventListener('click', closeResault)
        return () => {
            document.body.removeEventListener('click', closeResault)
        }
    }, [open])

    return (
        <div className="wrapper-input-find-guide">
            <label>Find your ultimate guide by Mallorca Magic</label>
            <div className="search-wrapper">
                <div className="input-wrapper">
                    <input type="text" onClick={() => setOpen(!open)} autoComplete="off" onChange={handleChange} value={searchValue} ref={result} />
                    {open &&
                        <div className="search-result">
                            {suggestions.map((item, _idx) => {
                                return (
                                    <div key={_idx} onClick={() => handleSelect(item)}>
                                        <img src={item.img} alt="Result" />
                                        <p>{item.text}</p>
                                    </div>
                                )
                            })}
                        </div>}
                </div>
                <button onClick={handleSearch}>Search</button>
            </div>
        </div>
    )
}