import axios from 'axios';
import React, { useCallback, useState, useRef } from 'react';
import Isvg from 'react-inlinesvg';
import { Title } from '../../../components/Title';
import { HOST_NAME, GOOGLE_RECAPTCHA_SITE_KEY } from '../../../config';
import Exit from '../../../images/svg/exit.svg';
import Heart from '../../../images/svg/heart.svg';
import { useContactPropertyModal } from '../Provider';
import ReCAPTCHA from "react-google-recaptcha";

export const ContactPropertyModal = () => {
  const { closeModal, data } = useContactPropertyModal();
  const [formValues, setFormValues] = useState({ name: "", lastName: "", email: "", phone: "", message: "", checkbox: false });
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState("");

  // Captcha
  const [recaptchaToken, setRecaptchaToken] = useState("");
  const recaptchaRef = useRef();

  const hanldeCloseModal = useCallback(() => {
    closeModal();
  }, [closeModal])

  const handlerChange = useCallback((e) => {
    setFormValues({ ...formValues, [e.target.name]: e.target.value })
  }, [formValues])

  const handleCheckBox = useCallback(() => {
    setFormValues({ ...formValues, checkbox: !formValues.checkbox })
  }, [formValues])

  const handleSubmit = useCallback(() => {
    // send data to server
    const payload = {};
    payload.slug = data.slug;
    payload.first_name = formValues.name;
    payload.last_name = formValues.lastName;
    payload.email = formValues.email;
    payload.phone = formValues.phone;
    payload.message = formValues.message;
    payload.recaptcha = recaptchaToken;

    setLoading(true);
    axios.post(`${HOST_NAME}api/property/contact/`, payload)
      .then(resp => {
        setSuccess(true);
      })
      .catch(error => {
        console.log(error);
        setError(error.response.data.error);
      })
      .finally(() => {
        setLoading(false);
        if (recaptchaRef.current) recaptchaRef.current.reset();
      });
  }, [data, formValues, recaptchaRef, recaptchaToken])


  return (
    <div className="property-contact-wrapper-2">
      <button className="exit" onClick={hanldeCloseModal}><Isvg src={Exit} /></button>
      {!success ?
        <>
          <div className="header">
            <Title text="More Information about the property" />
            <p>Contact with us and we will give the accurate and brief
              information about the property.</p>
            <h6>{data.title}</h6>
          </div>
          <div className="form-wrapper">
            <input type="text" name="name" placeholder="First Name" value={formValues.name} onChange={handlerChange} />
            <input type="text" name="lastName" placeholder="Last Name" value={formValues.lastName} onChange={handlerChange} />
            <input type="text" name="email" placeholder="Email" value={formValues.email} onChange={handlerChange} />
            <input type="text" name="phone" placeholder="Phone" value={formValues.phone} onChange={handlerChange} />
            <textarea name="message" placeholder="Say something...." onChange={handlerChange} />
            <div className="terms">
              <div className="checkbox" onClick={handleCheckBox}><div className={formValues.checkbox ? "check" : "ttt"}></div> <div></div> </div>
              <p>By checking here indicate that I have read and agree to the <a href="/terms-conditions">Terms and Conditions</a>.</p>
            </div>
            <ReCAPTCHA sitekey={GOOGLE_RECAPTCHA_SITE_KEY} onChange={token => setRecaptchaToken(token)} ref={recaptchaRef} />
            {error && <div className='error'>{error}</div>}
            <button disabled={!formValues.checkbox || loading} onClick={handleSubmit}>Send</button>
          </div>
        </>
        :
        <div className="success">
          <Title text="More Information about the property" />
          <Isvg src={Heart} />
          <h2>Thank you</h2>
          <p>Stay tuned. We will contact with you as soon as possible.</p>
        </div>}


    </div>
  )
}