import React, { useCallback, useState } from "react";
import Isvg from 'react-inlinesvg';
import { Link, useNavigate } from "react-router-dom";
import { EmailIcon, EmailShareButton, FacebookIcon, FacebookShareButton, LinkedinIcon, LinkedinShareButton, PinterestIcon, PinterestShareButton, TwitterIcon, TwitterShareButton } from "react-share";
import { HOST_NAME } from "../../../../config";
import Bed from '../../../../images/svg/bed.png';
import Delete from '../../../../images/svg/Delete.svg';
import Heart from '../../../../images/svg/icons_heart.svg';
import HeartFilled from '../../../../images/svg/icons_heart-filled.svg';
import Shower from '../../../../images/svg/shower.png';
import axios from "axios";
import { useRecoilValue } from "recoil";
import { AuthToken, LoggedUser } from "../../../../recoilState/User";

export const SecondGuideCars = ({ image, title, price, id, bedrooms, bathrooms, area, save, onDelete, isSaved }) => {
    const [, setLoading] = useState(false);
    const [saved, setSaved] = useState(isSaved);
    const token = useRecoilValue(AuthToken);
    const currentUser = useRecoilValue(LoggedUser);
    const navigate = useNavigate();
    const handleAddToFavorites = useCallback(() => {
        if (!currentUser.isLoggedIn) {
            navigate(`/login?next=${window.location.pathname}`);
            return;
        }
        setLoading(true);
        axios.post(`${HOST_NAME}api/saved/property/`, { slug: id }, { headers: { Authorization: `Token ${token}` } })
            .then(response => console.log(response.data))
            .catch(error => console.log(error))
            .finally(() => {
                setLoading(false);
                setSaved(true);
            });
    }, [token, id]);

    return (
        <div className="guide-card-second">
            {save && <button className="delete" onClick={() => onDelete(id)}><Isvg src={Delete} /></button>}
            <div className="wrapper">
                <Link to={`/property/${id}`}>
                    <img src={image} alt="property" />
                </Link>
                {!save &&
                    <div className="social-buttons">
                        <EmailShareButton
                            url={`${window.location.hostname}property/${id}`}
                            quote={title}
                            media={image}
                        >
                            <EmailIcon size={32} round />
                        </EmailShareButton>
                        <FacebookShareButton
                            url={`${window.location.hostname}property/${id}`}
                            quote={title}
                            media={image}
                        >
                            <FacebookIcon size={32} round />
                        </FacebookShareButton>
                        <TwitterShareButton
                            url={`${window.location.hostname}property/${id}`}
                            quote={title}
                            media={image}
                        >
                            <TwitterIcon size={32} round />
                        </TwitterShareButton>
                        <PinterestShareButton
                            url={`${window.location.hostname}property/${id}`}
                            quote={title}
                            media={image}
                        >
                            <PinterestIcon size={32} round />
                        </PinterestShareButton>
                        <LinkedinShareButton
                            url={`${window.location.hostname}property/${id}`}
                            quote={title}
                            media={image}
                        >
                            <LinkedinIcon size={32} round />
                        </LinkedinShareButton>
                    </div>
                }

            </div>
            <div className="info">
                <Link to={`/property/${id}`}>
                    <div>
                        <span> <p>{bedrooms}</p> <img src={Bed} alt="bed" /></span>
                    </div>

                    <div>
                        <span><p>{bathrooms}</p> <img src={Shower} alt="shower" /> <p>{area} m</p></span>
                    </div>
                </Link>
                <button onClick={handleAddToFavorites}> <Isvg src={saved ? HeartFilled : Heart} /> </button>
            </div>
            <div className="text">
                <h2>{title}</h2>
                <h6>{price}</h6>
            </div>

        </div>
    )
}