import React, { useCallback, useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import Bag from '../../images/guide/bag.png';
import Beach from '../../images/guide/beach.png';
import Hotels from '../../images/guide/hotels.png';
import Places from '../../images/guide/places.png';
import Run from '../../images/guide/run.png';
import arrowDown from '../../images/svg/arrow-down.svg';

export const guideMenu = [
    {
        id: 0,
        text: 'All Guides',
        src: Places,
    },
    {
        id: 1,
        text: 'Eat & Drink',
        src: Places
    },
    {
        id: 2,
        text: 'Activities',
        src: Run
    },
    {
        id: 3,
        text: 'Beaches',
        src: Beach
    },
    {
        id: 4,
        text: 'Art & Culture',
        src: Hotels
    },
    {
        id: 5,
        text: 'Shopping',
        src: Bag
    }
]

export const getUrlForCategory = (cat) => {
    const encoder = new URLSearchParams();
    encoder.append("cat", cat);
    return `/guide-landing?${encoder.toString()}`;
}

export const GuideLandingMenu = ({ handleGuideMenu }) => {
    const [showResults, setShowResults] = useState(false);
    const [currentMenu, setCurrentMenu] = useState({ text: "All Guides", category: "" });
    const result = useRef();

    const showOptions = useCallback(() => {
        setShowResults(!showResults)
    }, [showResults])

    const handleSelect = useCallback((selected) => {
        setShowResults(false);

        if (selected === 'All Guides')
            setCurrentMenu({ text: selected, category: '' });
        else
            setCurrentMenu({ text: selected, category: selected });
    }, [handleGuideMenu])

    useEffect(() => {
        handleGuideMenu(currentMenu.category);
    }, [currentMenu]);

    useEffect(() => {
        const closeResault = e => {
            if (showResults && result.current && !result.current.contains(e.target)) {
                setShowResults(false)
            }
        }
        document.body.addEventListener('click', closeResault)
        return () => {
            document.body.removeEventListener('click', closeResault)
        }
    }, [showResults])

    return (
        <div className="wrapper-guide-landing-menu">
            <div className="position">
                <button onClick={showOptions}> {currentMenu.text} <img src={arrowDown} alt="Arrow" /></button>
                {showResults &&
                    <div className="show-div">
                        {guideMenu.map((item, _idx) => {
                            return (
                                <p key={_idx} onClick={() => handleSelect(item.text)} ref={result}><img src={item.src} alt={item.text} />{item.text}</p>
                            )
                        })}
                    </div>
                }
            </div>

            {
                guideMenu.map(cat =>
                    cat.id !== 0 &&
                    <Link to={getUrlForCategory(cat.text)} className="content" key={cat.id}>
                        <img src={cat.src} alt={cat.text} />
                        <p>{cat.text}</p>
                    </Link>)
            }
        </div >

    )
}
