import { atom, selector } from "recoil";

export const allArea = atom({
    key: "allArea",
    default: []
});

export const AreaStartWith = atom({
    key: "AreaStartWith",
    default: ""
});

export const AllAreaSelector = selector({
    key: "AllAreaSelector",
    get: ({get}) => {
        const allData = get(allArea);
        const startWith = get(AreaStartWith);
        if(startWith.length === 0)  
            return allData.slice(0, 7);
        
        const fileteredArea = allData.filter(area => area.toLowerCase().startsWith(startWith.toLowerCase()));
        return fileteredArea.slice(0, 7);
      }
});
