import React from 'react';
import { ContactPropertyModalProvider } from './ContactProperty/Provider';
import { SavedGuidesModalProvider } from './Guides/Provider';
import { SavedPropertyModalProvider } from './Property/Provider';

export const Modal = ({ children }) => (
  <>
    <SavedPropertyModalProvider>
      <SavedGuidesModalProvider>
        <ContactPropertyModalProvider>
          {children}
        </ContactPropertyModalProvider>
      </SavedGuidesModalProvider>
    </SavedPropertyModalProvider>
  </>
);
