import React, { useCallback } from 'react';
import Isvg from 'react-inlinesvg';
import Exit from '../../../images/svg/exit.svg'
import { useSavedGuidesFormModal } from '../Provider';
import axios from 'axios';
import { useRecoilValue } from "recoil";
import { AuthToken } from "../../../recoilState/User";
import { HOST_NAME } from '../../../config';

export const SavedGuidesModal = () => {
  const { closeModal, data } = useSavedGuidesFormModal();
  const token = useRecoilValue(AuthToken);

  const hanldeCloseModal = useCallback(() => {
    closeModal();
  }, [closeModal])

  const perFormConfirmAction = useCallback(() => {
    axios.delete(`${HOST_NAME}api/saved/guide/`, { data: { all: true }, headers: { Authorization: `Token ${token}` } })
      .then(res => {
        closeModal();
        window.location.reload();
      }).catch(err => alert(err));
  }, [data]);

  return (
    <div className="saved-guides-modal">
      <div className="header">
        <h1>Remove All Guides?</h1>
        <button onClick={hanldeCloseModal}>
          <Isvg src={Exit} />
        </button>
      </div>
      <div className="modal-content">
        <p>Are you sure you want to remove all of the
          saved Guides?</p>
        <div className="buttons">
          <button className="save" onClick={perFormConfirmAction}>Yes, Remove</button>
          <button className="cancel" onClick={hanldeCloseModal}>Cancel</button>
        </div>
      </div>
    </div>
  )
}