import React, { createContext, useCallback, useContext, useMemo, useState } from 'react';
import { SavedGuidesModal } from '../Modal';

const SavedGuidesModalContext = createContext({});

export const SavedGuidesModalProvider = ({ children }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [data, setData] = useState(undefined)

  const openModal = useCallback((data) => {
    setData(data)
    setIsOpen(true);

  }, []);

  const closeModal = useCallback(() => {
    setData(undefined)
    setIsOpen(false);
  }, []);

  const value = useMemo(
    () => ({
      data,
      isOpen,
      openModal,
      closeModal,
    }),
    [closeModal, isOpen, openModal, data],
  );
  return (
    <SavedGuidesModalContext.Provider value={value}>
      {children}
      {isOpen &&
        <div className="modal-wrapper">
          <div className="content">
            <SavedGuidesModal />
          </div>
        </div>}
    </SavedGuidesModalContext.Provider >
  );
};

export const useSavedGuidesFormModal = () => useContext(SavedGuidesModalContext);
