import React, { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { useRecoilValue, useRecoilState } from "recoil";
import { AreaStartWith, AllAreaSelector } from '../../recoilState/propertyArea';
import Isvg from "react-inlinesvg";
import arrowDown from '../../images/svg/arrow-down.svg';
import { Link } from "react-router-dom";







export const PropertyOffers = ({ values, setValues }) => {

    const from = useMemo(() => ([
        {
            id: 1,
            text: "< 500.000",
            start: '',
            end: '500.000'
        },
        {
            id: 2,
            text: "< 1.000.000",
            start: '500.000',
            end: '1.000.000'
        },
        {
            id: 3,
            text: "< 2.000.000",
            start: '1.000.000',
            end: '2.000.000'
        },
        {
            id: 4,
            text: "< 3.000.000",
            start: '2.000.000',
            end: '3.000.000'
        },
        {
            id: 5,
            text: "< 5.000.000",
            start: '3.000.000',
            end: '5.000.000'
        },
        {
            id: 6,
            text: "< 10.000.000",
            start: '5.000.000',
            end: '10.000.000'
        },
        {
            id: 7,
            text: "> 10.000.000",
            start: '10.000.000',
            end: ''
        }
    ]), [])


    const property = useMemo(() => ([
        {
            id: 1,
            text: "House",
            value: "house"

        },
        {
            id: 2,
            text: "Apartment",
            value: "apartment"

        },
        {
            id: 3,
            text: "Villa",
            value: "villa"
        },
        {
            id: 4,
            text: "Finca",
            value: "finca"
        },
        {
            id: 5,
            text: "Plot",
            value: "plot"
        },
        {
            id: 6,
            text: "Commercial use",
            value: "commercial"
        }
    ]
    ), [])

    const [state, setState] = useState({ area: false, property: false, to: false })
    const [inputIndex, setInputIndex] = useState(undefined);
    const [currentInput, setCurrentInput] = useState(undefined);
    const [radioButton, setRadioButton] = useState(1);
    let reference = useRef([]);

    // All areas
    const areas = useRecoilValue(AllAreaSelector);
    const [, setAreaFilter] = useRecoilState(AreaStartWith);
    useEffect(() => setAreaFilter(""), []);

    reference.current = [0, 0, 0].map(
        (ref, index) => reference.current[index] = React.createRef()
    )

    const handleChange = useCallback((e) => {
        setValues({ ...values, [e.target.name]: e.target.value });

        if (e.target.name === 'area')
            setAreaFilter(e.target.value);
    }, [])

    const onSelect = useCallback((name, value) => {
        if (name === 'to') {
            const newValues = { ...values };
            from.filter(item => item.text === value)
                .forEach(item => {
                    newValues.from = item.start;
                    newValues.to = value;
                });
            setValues(newValues);
        } else setValues({ ...values, [name]: value });
        setState({ [name]: false })
    }, [values])

    const keepTrackOnInput = useCallback((name, index) => {
        setInputIndex(index);
        setCurrentInput([name]);
    }, [])

    const handleOpen = useCallback((name) => {
        setState({ [name]: !state[name] })
    }, [state]);

    const handleRadioButton = useCallback((value) => {
        setRadioButton(value);
        if (value === 1)
            setValues({ ...values, for: 'sale' });
        else
            setValues({ ...values, for: 'rent' });
    }, [values, setValues])

    useEffect(() => {
        setRadioButton(values.for === 'sale' ? 1 : 2);
    }, [values.for])

    useEffect(() => {
        const closeResault = e => {
            if (state[currentInput] && reference.current[inputIndex] && !reference.current[inputIndex].current.contains(e.target)) {
                setState({ ...state, [currentInput]: false })
                reference.current = [0, 0, 0].map(
                    (ref, index) => reference.current[index] = React.createRef()
                )
            }
        }
        document.body.addEventListener('click', closeResault)
        return () => {
            document.body.removeEventListener('click', closeResault)
        }
    }, [currentInput, inputIndex, state])

    return (
        <div className="property-offers-input">
            <div className="radio-buttons">
                <div className="radio" onClick={() => handleRadioButton(1)}>
                    <div className={radioButton === 1 ? "checked" : ""}>
                    </div>
                    <span>To Buy</span>
                </div>
                <div className="radio" onClick={() => handleRadioButton(2)}>
                    <div className={radioButton === 2 ? "checked" : ""}>
                    </div>
                    <span>To Rent</span>
                </div>
            </div>
            <div className="filters">
                <button className="button-area">Area</button>

                <div className="input-wrapper">
                    <input type="text" placeholder="Choose Area" autoComplete="off" className="input-area" ref={reference.current[0]} name="area" value={values.area} onFocus={() => handleOpen('area')} onClick={() => keepTrackOnInput('area', 0)} onChange={handleChange} />
                    {!state.area && <Isvg src={arrowDown} />}
                    {state.area &&
                        <div className="resutlt-search">
                            {areas.map((item, _idx) => {
                                return (
                                    <p key={_idx} onClick={() => onSelect("area", item)}>{item}</p>
                                )
                            })}

                        </div>
                    }
                </div>

                <button className="button-property">Property Type</button>

                <div className="input-wrapper">
                    <input type="text" placeholder="Choose Property Type" autoComplete="off" className="input-property" ref={reference.current[1]} name="property" value={values.property} onFocus={() => handleOpen('property')} onClick={() => keepTrackOnInput('property', 1)} onChange={handleChange} />
                    {!state.property && <Isvg src={arrowDown} />}
                    {state.property &&
                        <div className="resutlt-search">
                            {property.map((item, _idx) => {
                                return (
                                    <p key={_idx} onClick={() => onSelect('property', item.value)}>{item.text}</p>
                                )
                            })}

                        </div>
                    }
                </div>

                <button className="button-price">Price</button>

                <div className="input-wrapper">
                    <input className="input-to" type="text" placeholder="To" autoComplete="off" name="to" ref={reference.current[2]} value={values.to} onFocus={() => handleOpen('to')} onClick={() => keepTrackOnInput('to', 3)} onChange={handleChange} />
                    {!state.to && <Isvg src={arrowDown} />}
                    {state.to &&
                        <div className="resutlt-search">
                            {from.map((item, _idx) => {
                                return (
                                    <p key={_idx} onClick={() => onSelect('to', item.text)}>{item.text}</p>
                                )
                            })}
                        </div>
                    }
                </div>
            </div>
            <Link to={`/property-selector/?fromPrice${values.from}=&toPrice=${values.to}&area=${values.area}&type=${values.property}&for=${values.for}`}><button className="search">Search</button></Link>
        </div>
    )
}