import React, { useCallback } from 'react';
import Isvg from 'react-inlinesvg';
import Exit from '../../../images/svg/exit.svg'
import { useSavedPropertyFormModal } from '../Provider';
import axios from 'axios';
import { useRecoilValue } from "recoil";
import { AuthToken } from "../../../recoilState/User";
import { HOST_NAME } from '../../../config';

export const SavedPropertyModal = () => {
  const { closeModal } = useSavedPropertyFormModal();
  const token = useRecoilValue(AuthToken);

  const hanldeCloseModal = useCallback(() => {
    closeModal();
  }, [closeModal])

  const deleteAllProperties = useCallback(() => {
    axios.delete(`${HOST_NAME}api/saved/property/`, { data: { all: true }, headers: { Authorization: `Token ${token}` } })
      .then(res => {
        closeModal();
        window.location.reload();
      }).catch(err => alert(err));
  }, [])

  return (
    <div className="saved-property-modal">
      <div className="header">
        <h1>Remove All Properties?</h1>
        <button onClick={hanldeCloseModal}>
          <Isvg src={Exit} />
        </button>
      </div>
      <div className="modal-content">
        <p>Are you sure you want to remove all of the <br />
          saved properties?</p>
        <div className="buttons">
          <button className="save" onClick={deleteAllProperties}>Yes, Remove</button>
          <button className="cancel" onClick={hanldeCloseModal}>Cancel</button>
        </div>
      </div>
    </div>
  )
}