import React, { useMemo } from "react";
import SlickSlider from "react-slick";
import { GuideCard } from '../GuideCard/index';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { SecondGuideCars } from "../GuideCard/SecondGuideCard";
import { ThirdGuideCard } from "../GuideCard/ThirdGuideCard";


export const Slider = ({ source, insta, type, arrows, secondGuideCardProp, thirdGuideCardProp }) => {
    const settings = useMemo(() => ({
        dots: true,
        infinite: true,
        speed: 700,
        slidesToShow: 3,
        slidesToScroll: 1,
        arrows: arrows,
        autoplay: true,
        autoplaySpeed: 7000,
        responsive: [
            {
                breakpoint: 767,
                settings: {
                    slidesToShow: 1,
                    centerMode: true,
                    centerPadding: '20%',
                }
            },
            {
                breakpoint: 991,
                settings: {
                    slidesToShow: 2,
                    centerMode: true,
                    centerPadding: '5%',
                }
            }
        ]
    }), [arrows]);
    return (
        <div className={`slick-slider-wrapper ${insta ? 'insta-slider' : ''}`}>
            <SlickSlider {...settings}>
                {!secondGuideCardProp && !thirdGuideCardProp && source.map((item, _idx) => {
                    return (
                        <GuideCard key={_idx} image={item.src} title={item.title} desc={item.desc} insta={insta} id={item.id} type={type} isSaved={item.is_saved} />
                    )
                })}
                {secondGuideCardProp && source.map((item, _idx) => {
                    return (
                        <SecondGuideCars key={_idx} image={item.thumbnail[0]} title={item.details.title} price={`${item.details.price_unit} ${item.details.price_amount}`}
                            bedrooms={item.details.bedrooms} bathrooms={item.details.bathrooms} area={item.details.size_construction} id={item.slug} />)
                })}
                {thirdGuideCardProp && source.map((item, _idx) => {
                    return (
                        <ThirdGuideCard
                            key={_idx} image={item.thumbnail[0]} title={item.details.title[0]} price={`${item.details.price_unit} ${item.details.price_amount}`}
                            id={item.slug} rating={item.rating.average} ratingCount={item.rating.total}
                        />
                    )
                })}
            </SlickSlider>
        </div>
    )
}