import axios from 'axios';
import React, { lazy, Suspense, useCallback, useEffect, useState } from 'react';
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { useRecoilState, useRecoilValue } from 'recoil';
import './App.css';
import LoadingBar from './components/LoadingBar';
import { HOST_NAME } from './config';
import { GoogleMapScript } from './googleMapScript';
import { allArea } from './recoilState/propertyArea';
import { AuthToken, LoggedUser, setUserTokenCookie } from "./recoilState/User";
import ScrollIntoView from './scrollIntoView';
import { Home } from './views/Home';
const GuideLanding = lazy(() => import('./views/GuideLanding'));
const Contact = lazy(() => import('./views/Contact'));
const PropertySelector = lazy(() => import('./views/PropertySelector'));
const GuideInstagram = lazy(() => import('./views/GuideInstagram'));
const MediaKit = lazy(() => import('./views/MadiaKit'));
const Property = lazy(() => import('./views/Property'));
const GuideBlogPost = lazy(() => import('./views/GuideBlogPost'));
const SignUp = lazy(() => import('./views/Signup'));
const Login = lazy(() => import('./views/Login'));
const SavedProperty = lazy(() => import('./views/SavedProperty'));
const SavedGuides = lazy(() => import('./views/SavedGuides'));
const NotFound = lazy(() => import('./views/404'));
const PropertyContact = lazy(() => import('./views/PropertyContact'));
const AccountSettings = lazy(() => import('./views/AccountSettings'));
const PrivacyPolicy = lazy(() => import('./views/PrivacyPolicy'));
const TermsConditions = lazy(() => import('./views/TermsConditions'));
const ActivitiesLanding = lazy(() => import('./views/ActivitiesLanding'));
const ActivitiesLink = lazy(() => import('./views/ActivitiesLink'));
const ResetPassword = lazy(() => import('./views/ResetPassword'));
const ForgetPassword = lazy(() => import('./views/ForgotPassword'));

export const App = () => {

    const [mapinit, setMapInit] = useState();
    const callback = useCallback(() => {
        setMapInit({ mapinit: true });
    }, [])
    window.googleMapsCallback = callback;

    const [, setAreas] = useRecoilState(allArea);
    useEffect(() => {
        axios.get(HOST_NAME + "api/property/locations/")
            .then(response => {
                if (response.status === 200) {
                    setAreas(response.data.locations);
                }
            }).catch(error => console.log(error));
    }, []);

    // Login/out when token changes
    const authToken = useRecoilValue(AuthToken);
    const [, setUser] = useRecoilState(LoggedUser);
    useEffect(() => {
        if (authToken.length > 0) {
            setUserTokenCookie(authToken);
            axios.post(`${HOST_NAME}api/accounts/user/`, {}, { headers: { Authorization: `Token ${authToken}` } })
                .then(response => {
                    if (response.status === 200) {
                        console.log("Logge user", response.data);
                        setUser({
                            isLoggedIn: true,
                            user: response.data
                        });
                    }
                }).catch(error => {
                    console.log(error);
                });
        } else {
            setUserTokenCookie('', -1);
            setUser({
                isLoggedIn: false,
                user: {
                    username: "",
                    email: "",
                    news_letters: false,
                    guide_alert: false,
                    offers_alert: false,
                }
            });
        }

        // axios default header
        if (authToken.length > 0)
            axios.defaults.headers.common['Authorization'] = `Token ${authToken}`;
        else
            delete axios.defaults.headers.common['Authorization'];
    }, [authToken]);

    return (
        <>
            <GoogleMapScript API_KEY="AIzaSyCzKrCwhiFuLc5VgrncY-VZA-qzegAypCM" />

            <Router>
                <ScrollIntoView />
                <Suspense fallback={<LoadingBar />} >
                    <Routes>
                        <Route path="/" element={<Home {...mapinit} />} />
                        <Route path="/guide-landing" element={<GuideLanding />} />
                        <Route path="/contact" element={<Contact {...mapinit} />} />
                        <Route path="/property-selector" element={<PropertySelector />} />
                        <Route path="/guide-instagram/:slug" element={<GuideInstagram />} />
                        <Route path="/media-kit" element={<MediaKit />} />
                        <Route path="/property/:slug" element={<Property {...mapinit} />} />
                        <Route path="/guide-blog-post/:slug" element={<GuideBlogPost />} />
                        <Route path="/signup" element={<SignUp />} />
                        <Route path="/login" element={<Login />} />
                        <Route path="/saved-property" element={<SavedProperty />} />
                        <Route path="/saved-guides" element={<SavedGuides />} />
                        <Route path="*" element={<NotFound />} />
                        <Route path="/property-contact" element={<PropertyContact />} />
                        <Route path="/account-settings" element={<AccountSettings />} />
                        <Route path="/privacy-policy" element={<PrivacyPolicy />} />
                        <Route path="/terms-conditions" element={<TermsConditions />} />
                        <Route path="/activities-landing" element={<ActivitiesLanding />} />
                        <Route path="/activities-link/:slug" element={<ActivitiesLink />} />
                        <Route path="/reset-password" element={<ResetPassword />} />
                        <Route path="/forget-password" element={<ForgetPassword />} />
                    </Routes>
                </Suspense>
            </Router>
        </>

    )
}

export default Home