import React from "react";
import { Link } from "react-router-dom";
import {
    EmailIcon, EmailShareButton, FacebookIcon, FacebookShareButton, LinkedinIcon, LinkedinShareButton, PinterestIcon, PinterestShareButton, TwitterIcon, TwitterShareButton
} from "react-share";
import { StarRating } from "../../../StarRating";


export const ThirdGuideCard = ({ image, title, id, price, rating, ratingCount }) => {
    let url = `/activities-link/${id}`;

    return (
        <div className="third-guide-card">
            <div className="wrapper">
                <Link to={url}>
                    <img src={image} alt="property" />
                </Link>

                <div className="social-buttons">
                    <EmailShareButton
                        url={`${window.location.hostname}${url}`}
                        quote={title}
                        media={image}
                    >
                        <EmailIcon size={32} round />
                    </EmailShareButton>
                    <FacebookShareButton
                        url={`${window.location.hostname}${url}`}
                        quote={title}
                        media={image}
                    >
                        <FacebookIcon size={32} round />
                    </FacebookShareButton>
                    <TwitterShareButton
                        url={`${window.location.hostname}${url}`}
                        quote={title}
                        media={image}
                    >
                        <TwitterIcon size={32} round />
                    </TwitterShareButton>
                    <PinterestShareButton
                        url={`${window.location.hostname}${url}`}
                        quote={title}
                        media={image}
                    >
                        <PinterestIcon size={32} round />
                    </PinterestShareButton>
                    <LinkedinShareButton
                        url={`${window.location.hostname}${url}`}
                        quote={title}
                        media={image}
                    >
                        <LinkedinIcon size={32} round />
                    </LinkedinShareButton>
                </div>

            </div>

            <div className="text">
                <Link to={url}>
                    <h2>{Array.isArray(title) ? title.join('') : title}</h2>
                </Link>
                <div className="star-rating">
                    {
                        [0, 1, 2, 3, 4].map((starIndex) => <StarRating key={starIndex} value={(rating - starIndex) * 100} color="#FFCB45" />)
                    }
                    <span>({ratingCount})</span>
                </div>
                <h3>{price}</h3>
            </div>

        </div>
    )
}