import React from 'react'

export const StarRating = ({ value, color }) => {
    return (

        <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width="17" height="16" viewBox="0 0 32 32">
            <defs >
                <linearGradient id={`grad${value}`}>
                    <stop offset={`${value}%`} stopColor={color} />
                    <stop offset={`${100 - value}%`} stopColor="#F2F2F2" />
                </linearGradient>
            </defs>
            <path fill={`url(#grad${value})`} d="M20.388,10.918L32,12.118l-8.735,7.749L25.914,31.4l-9.893-6.088L6.127,31.4l2.695-11.533L0,12.118
  l11.547-1.2L16.026,0.6L20.388,10.918z"/>
        </svg>

    )
}