import React, { useCallback, useEffect, useRef, useState } from "react";
import { ElfsightWidget } from 'react-elfsight-widget';
import Isvg from 'react-inlinesvg';
import { Link } from "react-router-dom";
import { useRecoilState, useRecoilValue } from "recoil";
import About from '../images/svg/about.svg';
import BxCycling from '../images/svg/bx_cycling.svg';
import Close from '../images/svg/Close.svg';
import ContactUs from '../images/svg/contactus.svg';
import Find from '../images/svg/find.svg';
import hamburger from '../images/svg/hamburger.svg';
import Home from '../images/svg/home.svg';
import Logout from '../images/svg/logout.svg';
import MMLogo from '../images/svg/MM Full Logo.svg';
import Pin from '../images/svg/pinLocation.svg';
import Property from '../images/svg/property.svg';
import SavedProperties from '../images/svg/saved-properties.svg';
import Fb from '../images/svg/social-networks/sidebar/fb.svg';
import Insta from '../images/svg/social-networks/sidebar/insta.svg';
import Pintrest from '../images/svg/social-networks/sidebar/pintrest.svg';
import TikTok from '../images/svg/social-networks/sidebar/tiktok.svg';
import Twitter from '../images/svg/social-networks/sidebar/twitter.svg';
import User from '../images/svg/user-blue.svg';
import { AuthToken, LoggedUser } from "../recoilState/User";

const getWindowsDimensions = () => {
  const { innerHeight: height, innerWidth: width } = window;
  return {
    height,
    width,
  };
};

export const Navbar = ({ coverImage, coverText, blog, blog2 }) => {
  const [menu, setMenu] = useState(false);
  const [windowDimensions, setWindowDimensions] = useState(getWindowsDimensions());
  const outsideMenuClick = useRef(null);
  const [, setAuthToken] = useRecoilState(AuthToken);
  const currentUser = useRecoilValue(LoggedUser);

  useEffect(() => {
    const closeResault = e => {
      if (menu && outsideMenuClick.current && !outsideMenuClick.current.contains(e.target)) {
        setMenu(false)
      }
    }
    document.body.addEventListener('click', closeResault)
    return () => {
      document.body.removeEventListener('click', closeResault)
    }
  }, [menu]);

  useEffect(() => {
    const handleResize = () => {
      setWindowDimensions(getWindowsDimensions());
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);

  }, []);

  const openSideBar = useCallback(() => {
    setMenu(!menu);
  }, [menu])

  const logout = useCallback(() => {
    setAuthToken('');
  }, [])

  return (
    <div className="navbar-wrapper">
      <div className="navbar">
        <div className="weather">
          <ElfsightWidget widgetID="3668bf8e-6cdb-4e75-b92a-1aff1c0e9b68" />
        </div>
        <div className="logo">
          <Link to="/">
            <Isvg src={MMLogo} />
          </Link>
        </div>
        <div className="right-side-menu">
          <div className="languages">
            <a href="https://www.mallorca-magic.com"><button>EN</button></a>
            /
            <a href="https://www.mallorcamagic.de"><button>ES</button></a>
            /
            <a href="https://mallorcamagic.es/"><button>DE</button></a>
          </div>
          <div className="wrapper-menu" ref={outsideMenuClick} >
            <button className="hamburger" onClick={() => setMenu(!menu)}>
              <Isvg src={hamburger} />
            </button>
            {menu && windowDimensions.width > 991 &&

              <div className="menu">
                <h6>Explore Mallorca Magic now!</h6>
                {currentUser.isLoggedIn && <p>Welcome {currentUser.user.username}</p>}
                <div className="content">
                  <div className="left">
                    <div><Isvg src={Home} /><Link to="/">Home</Link></div>
                    <div><Isvg src={Property} /><Link to="/property-selector">Property Offers</Link></div>
                    <div><Isvg src={BxCycling} /><Link to="/activities-landing">Activities</Link></div>
                    {currentUser.isLoggedIn &&
                      <>
                        <div><Isvg src={SavedProperties} /> <Link to="/saved-property" className="nav-link">Saved properties</Link> </div>
                        <div><Isvg src={ContactUs} /><Link to="/contact">Contact Us</Link></div>
                        <div onClick={logout}><Isvg src={Logout} />Logout</div>
                      </>
                    }

                  </div>
                  <div className="right">
                    <div><Isvg src={About} /><Link to="/media-kit">About</Link></div>
                    <div><Isvg src={Find} /><Link to="/guide-landing">Find your guide</Link></div>

                    {currentUser.isLoggedIn &&
                      <>
                        < div ><Isvg src={Pin} /> <Link to="/saved-guides">Saved guides</Link> </div>
                        <div><Isvg src={User} /> <Link to="/account-settings">My account</Link> </div>
                      </>}
                    {!currentUser.isLoggedIn && <div><Isvg src={ContactUs} /><Link to="/contact">Contact Us</Link></div>}


                  </div>
                </div>
                {!currentUser.isLoggedIn &&
                  <div className="buttons">
                    <Link to="/signup"><button>Sign Up</button></Link>
                    <Link to="/login"><button>Log In</button></Link>
                  </div>
                }
              </div>
            }
          </div>
        </div>

        {
          windowDimensions.width < 991 &&
          <div className="mobile-navbar-wrapper">
            <div className={menu ? 'navbar-open' : 'navbar-close'}>
              <div className={menu ? 'content-open' : 'content-close'} >

                <div style={{ overflow: 'hidden' }}>
                  <ul className="content">
                    <div className="navbar-header">
                      <div className="weather">
                        <ElfsightWidget widgetID="3668bf8e-6cdb-4e75-b92a-1aff1c0e9b68" />
                      </div>
                      <div className="logo">
                        <Isvg src={MMLogo} />
                      </div>
                      <button onClick={openSideBar} className="close-icon"><Isvg src={Close}></Isvg></button>
                    </div>
                    <li><Isvg src={Home} /> <Link to='/' className="nav-link">Home</Link></li>
                    <li> <Isvg src={Find} /><Link to="/guide-landing" className="nav-link">Find your guide</Link> </li>
                    <li><Isvg src={Property} /> <Link to="/property-selector" className="nav-link">Property offers</Link> </li>
                    <li><Isvg src={BxCycling} /> <Link to="/activities-landing" className="nav-link">Activities</Link> </li>
                    {currentUser.isLoggedIn &&
                      <>
                        <li id="saved-guides"><Isvg src={Pin} /> <Link to="/saved-guides" className="nav-link">Saved guides</Link> </li>
                        <li><Isvg src={SavedProperties} /> <Link to="/saved-property" className="nav-link">Saved properties</Link> </li>
                        <li><Isvg src={User} /> <Link to="/account-settings" className="nav-link">My account</Link> </li>
                      </>
                    }
                    <li><Isvg src={About} /> <Link to="/media-kit" className="nav-link">About</Link> </li>
                    <li><Isvg src={ContactUs} /> <Link to="/contact" className="nav-link">Contact us</Link> </li>
                    {currentUser.isLoggedIn && <li className="logout" onClick={logout}><Isvg src={Logout} /> Logout </li>}

                    <div className="book-now">
                      {!currentUser.isLoggedIn &&
                        <div className="auth-buttons">
                          <Link to="/signup"><button>Sign Up</button></Link>
                          <Link to="/login"><button className="logout-button">Log In</button></Link>
                        </div>
                      }
                      <div className="social-icons">
                        <a href="https://www.facebook.com/mallorca.is.magic" target="_blank" rel="noopener noreferrer"><Isvg src={Fb} /></a>
                        <a href="https://www.instagram.com/mallorcamagic/" target="_blank" rel="noopener noreferrer"><Isvg src={Insta} /></a>
                        <a href="https://www.pinterest.de/mallorca_magic/" target="_blank" rel="noopener noreferrer"><Isvg src={Pintrest} /></a>
                        <a href="https://twitter.com/mallorca_magic" target="_blank" rel="noopener noreferrer"> <Isvg src={Twitter} /></a>
                        <a href="https://www.tiktok.com/@mallorca.magic" target="_blank" rel="noopener noreferrer"><Isvg src={TikTok} /></a>
                      </div>
                    </div>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        }

      </div >
      <div className="cover-image">
        {coverImage &&
          <img src={coverImage} alt="Home Cover" className="home-cover" />}
        {coverText &&
          <div className="cover-text"> <h1 dangerouslySetInnerHTML={{__html: coverText}}></h1> </div>}
      </div>
    </div >

  )
}

