import { useEffect } from 'react';

export const GoogleMapScript = (props) => {
    useEffect(() => {
        var ref = window.document.getElementsByTagName("script")[0];
        var script = window.document.createElement("script");
        // script.src = "https://maps.googleapis.com/maps/api/js?sensor=false&key=&callback=googleMapsCallback";
        script.src = "https://maps.googleapis.com/maps/api/js?sensor=false&key=" + props.API_KEY + "&callback=googleMapsCallback&language=en&region=eu";
        script.async = true;
        script.defer = true;

        ref.parentNode.insertBefore(script, ref);
    }, [])

    return null;

}