import React, { createContext, useCallback, useContext, useMemo, useState } from 'react';
import { SavedPropertyModal } from '../Modal';

const SavedPropertyModalContext = createContext({});

export const SavedPropertyModalProvider = ({ children }) => {
  const [isOpen, setIsOpen] = useState(false);
  //passed data to the modal
  const [data, setData] = useState(undefined);

  const openModal = useCallback((data) => {
    setIsOpen(true);
    setData(data)
  }, []);

  const closeModal = useCallback(() => {
    setIsOpen(false);
    setData(undefined)
  }, []);

  const value = useMemo(
    () => ({
      data,
      isOpen,
      openModal,
      closeModal,
    }),
    [closeModal, isOpen, openModal, data],
  );
  return (
    <SavedPropertyModalContext.Provider value={value}>
      {children}
      {isOpen &&
        <div className="modal-wrapper">
          <div className="content">
            <SavedPropertyModal />
          </div>
        </div>}
    </SavedPropertyModalContext.Provider >
  );
};

export const useSavedPropertyFormModal = () => useContext(SavedPropertyModalContext);
