import React, { useCallback, useState } from "react";
import Isvg from 'react-inlinesvg';
import { Link, useNavigate } from "react-router-dom";
import {
    EmailIcon, EmailShareButton, FacebookIcon, FacebookShareButton, LinkedinIcon, LinkedinShareButton, PinterestIcon, PinterestShareButton, TwitterIcon, TwitterShareButton
} from "react-share";
import Delete from '../../../images/svg/Delete.svg';
import Heart from '../../../images/svg/icons_heart.svg';
import HeartFilled from '../../../images/svg/icons_heart-filled.svg';
import { HOST_NAME } from "../../../config";
import axios from "axios";
import { useRecoilValue } from "recoil";
import { AuthToken, LoggedUser } from "../../../recoilState/User";


export const GuideCard = ({ image, title, desc, insta, id, type, save, isSaved, onDelete }) => {
    let url = `/guide-instagram/${id}`;
    if (type && type === "props")
        url = `/property/${id}`;

    const [, setLoading] = useState(false);
    const [saved, setSaved] = useState(isSaved);
    const token = useRecoilValue(AuthToken);
    const currentUser = useRecoilValue(LoggedUser);
    const navigate = useNavigate();

    const handleAddToFavorites = useCallback(() => {
        if (!currentUser.isLoggedIn) {
            navigate(`/login?next=${window.location.pathname}`);
            return;
        }
        let url = `${HOST_NAME}api/saved/guide/`;
        if (type && type === "props")
            url = `${HOST_NAME}api/saved/property/`;

        setLoading(true);
        axios.post(url, { slug: id }, { headers: { Authorization: `Token ${token}` } })
            .then(response => console.log(response.data))
            .catch(error => console.log(error))
            .finally(() => {
                setLoading(false);
                setSaved(true);
            });
    }, [token, type, id]);

    return (
        <div className="guide-card">
            {save && <button className="delete" onClick={() => onDelete(id)}><Isvg src={Delete} /></button>}
            <div className="wrapper">
                <Link to={url}>
                    <img src={image} alt="property" />
                </Link>
                {!insta && !save &&
                    <div className="social-buttons">
                        <EmailShareButton
                            url={`${window.location.hostname}${url}`}
                            quote={title}
                            media={image}
                        >
                            <EmailIcon size={32} round />
                        </EmailShareButton>
                        <FacebookShareButton
                            url={`${window.location.hostname}${url}`}
                            quote={title}
                            media={image}
                        >
                            <FacebookIcon size={32} round />
                        </FacebookShareButton>
                        <TwitterShareButton
                            url={`${window.location.hostname}${url}`}
                            quote={title}
                            media={image}
                        >
                            <TwitterIcon size={32} round />
                        </TwitterShareButton>
                        <PinterestShareButton
                            url={`${window.location.hostname}${url}`}
                            quote={title}
                            media={image}
                        >
                            <PinterestIcon size={32} round />
                        </PinterestShareButton>
                        <LinkedinShareButton
                            url={`${window.location.hostname}${url}`}
                            quote={title}
                            media={image}
                        >
                            <LinkedinIcon size={32} round />
                        </LinkedinShareButton>
                    </div>
                }
            </div>
            {!insta &&
                <div className="text">
                    <div>
                        <h2>{Array.isArray(title) ? title.join('') : title}</h2>
                        <button onClick={handleAddToFavorites}><Isvg src={saved ? HeartFilled : Heart} /></button>
                    </div>
                    <h6>{desc}</h6>


                </div>
            }
        </div>
    )
}