import React, { useCallback, useEffect, useRef, useState } from 'react';
import mapIcon from '../../images/map_icon_v2.svg';
import Bed from '../../images/svg/bed.png';
import Shower from '../../images/svg/shower.png';
import classes from './PropertiesMap.module.css';
import { HOST_NAME } from '../../config';
import axios from 'axios';

const getContentString = (img, bedrooms, showers, area, title, price, slug) => {
    const contentString = `
        <a href="/property/${slug}" style="text-decoration: none">
            <div class="guide-card-second ${classes['map-info-window']}">
                <div class="wrapper">
                    <img src="${img}" alt="property">
                </div>
                <div class="info">
                    <div>
                        <div>
                            <span><p>${bedrooms}</p> <img src="${Bed}" alt="bed"></span>
                        </div>
                        <div>
                            <span><p>${showers}</p> <img src="${Shower}" alt="shower"> <p>${area} m</p></span>
                        </div>
                    </div>
                </div>
                <div class="text">
                    <h2>${title}</h2>
                    <h6>${price}</h6>
                </div>
            </div>
        </a>
    `;
    return contentString;
}

export const PropertiesMap = (props) => {
    const ref = useRef(null);
    const [state, setState] = useState({});
    const [properties, setProperties] = useState([]);
    const [markers, setMarkers] = useState([]);
    const [map, setMap] = useState(null);
    useEffect(() => {
        const params = new URLSearchParams();
        params.append("limit", "100");
        axios.get(HOST_NAME + "api/property/search/", { params: params })
            .then(resp => {
                console.log(resp);
                if (resp.status === 200) {
                    setProperties(resp.data.data);
                }
            });
    }, []);

    const initMap = useCallback(() => {
        setState({ _mapInit: true });
        const latLng = new window.google.maps.LatLng(39.6292777, 2.745214);

        const map = new window.google.maps.Map(ref.current, {
            zoom: 10,
            center: latLng,
            mapTypeId: window.google.maps.MapTypeId.ROADMAP,
            disableDefaultUI: true,
            gestureHandling: "gestureHandling",

        });
        setMap(map);

        // Draw markers
        const allMarkers = [];
        properties.forEach(property => {
            const marker = new window.google.maps.Marker({
                position: new window.google.maps.LatLng(parseFloat(property.details.loc_lat), parseFloat(property.details.loc_lng)),
                map: map,
                icon: mapIcon
            });

            // Info window
            const infoWindow = new window.google.maps.InfoWindow({
                content: getContentString(property.thumbnail[0], property.details.bedrooms, property.details.bathrooms, property.details.size_construction,
                    property.details.title, `${property.details.price_amount} ${property.details.price_unit}`, property.slug)
            });
            marker.addListener('click', function () {
                infoWindow.open(map, marker);
            });
            allMarkers.push({ marker, infoWindow, slug: property.slug });
        });
        setMarkers(allMarkers);
    }, [properties])

    useEffect(() => {
        if (ref.current) {
            initMap();
        }
    }, [initMap, state._mapInit])

    useEffect(() => {
        markers.forEach(marker => marker.infoWindow.close());
        markers.forEach(marker => {
            if (marker.slug === props.active) {
                marker.infoWindow.open(map, marker.marker);
            }
        });
    }, [props.active]);

    return (
        <div>
            {props.mapinit &&
                <div className="map" ref={(input) => { ref.current = input; }}>
                </div>
            }
        </div>
    );
}