import axios from 'axios';
import React, { useCallback, useEffect, useState } from 'react';
import { ElfsightWidget } from 'react-elfsight-widget';
import { Link } from 'react-router-dom';
import { FindGuide } from '../components/FindGuide';
import { Slider } from '../components/Guide/Slider';
import { getUrlForCategory, guideMenu } from '../components/GuideLandingMenu';
// import { Dropdown } from '../components/Input/Dropdown';
import { useRecoilState, useRecoilValue } from "recoil";
import { PropertiesMap } from '../components/PropertiesMap';
import { PropertyOffers } from '../components/PropertyOffers';
import { Subscribe } from '../components/Subscrube';
import { Title } from '../components/Title';
import { HOST_NAME } from '../config';
import { DefaultLayout } from '../containers/DefaultLayout';
import HomeCover from '../images/home-cover.png';
import { AllAreaSelector, AreaStartWith } from '../recoilState/propertyArea';
import { SecondGuideCars } from '../components/Guide/GuideCard/SecondGuideCard';


export const Home = (props) => {
  const [placesQuery, setPlacesQuery] = useState('');
  const [activeMarker, setActiveMarker] = useState("");

  // Property
  const [queries, setQueries] = useState({
    area: '',
    from: '',
    to: '',
    property: '',
    for: 'sale'
  });
  const [properties, setProperties] = useState([]);

  // All areas
  const areas = useRecoilValue(AllAreaSelector);
  const [, setAreaFilter] = useRecoilState(AreaStartWith);
  useEffect(() => setAreaFilter(placesQuery), [placesQuery]);

  // Gallery properties
  const [galleryProperties, setGalleryProperties] = useState([]);
  const loadGalleryProperties = useCallback((area) => {
    const params = new URLSearchParams();
    params.append("limit", "4");
    params.append("area", area);
    axios.get(HOST_NAME + "api/property/search/", { params: params })
      .then(resp => {
        if (resp.status === 200) {
          setGalleryProperties(resp.data.data);
        }
      });
  }, [setGalleryProperties]);
  useEffect(() => loadGalleryProperties(""), []);

  // Activities
  const [activities, setActivities] = useState([]);
  useEffect(() => {
    const params = new URLSearchParams();
    params.append("limit", "10");
    axios.get(`${HOST_NAME}api/activities/`, { params: params })
      .then(resp => {
        if (resp.status === 200) {
          setActivities(resp.data.data);
        }
      }).catch(err => console.log(err));
  }, []);

  const handlePlacesChange = useCallback((value) => {
    setPlacesQuery(value);
  }, [])

  const handlePlacesQuery = useCallback((value) => {
    setPlacesQuery(value);
    loadGalleryProperties(value);
  }, [])

  const performSearch = useCallback(() => {
    const params = new URLSearchParams();
    params.append("limit", "8");
    params.append("toPrice", queries.to);
    params.append("area", queries.area);
    params.append("fromPrice", queries.from);
    params.append("type", queries.property);
    params.append("for", queries.for);

    axios.get(HOST_NAME + "api/property/search/", { params: params })
      .then(resp => {
        if (resp.status === 200) {
          setProperties(resp.data.data);
        }
      })
  }, [queries]);

  // Load initial data
  useEffect(performSearch, []);

  // Guides
  const [guideQuery, setGuideQuery] = useState("");
  const [guides, setGuides] = useState([]);
  const loadGuides = useCallback(() => {
    const url = HOST_NAME + "api/guide/search/";
    const params = new URLSearchParams();
    params.append("limit", "10");
    params.append("cat", "");
    params.append("q", guideQuery);
    axios.get(url, { params: params }).then(resp => {
      if (resp.status === 200) {
        const transformedGuides = resp.data.data.map((item) => {
          return {
            src: item.thumbnail[0],
            title: item.details.title,
            desc: item.details.creator,
            id: item.slug
          }
        });
        setGuides(transformedGuides);
      }
    });
  }, [guideQuery]);
  useEffect(loadGuides, []);

  return (
    <div className="home-wrapper">
      <DefaultLayout coverImage={HomeCover} coverText="love : life : living" >

        <div className="find-guide">
          <Title text="Find Your Guide" />
          <FindGuide onQueryChange={setGuideQuery} />
          <div className="guide">
            {
              guideMenu.map((cat, _idx) =>
                cat.id !== 0 &&
                <div key={_idx}>
                  <Link to={getUrlForCategory(cat.text)} className="content" key={cat.id}>
                    <img src={cat.src} alt={cat.text} />
                    <p>{cat.text}</p>
                  </Link>
                </div>)
            }
          </div>
        </div>

        <div className="new-guides">
          <Title text="New Guides" />
          <div className="wrapper-slider">
            <Slider source={guides} />
          </div>
        </div>


        <div className="property-offers-wrapper">
          <Title text="Property Offers" />
          <div className="property-offers">
            <PropertyOffers values={queries} setValues={setQueries} />
          </div>
          <div className="wrapper-slider">
            {properties.length <= 3 ?
              <div className="item-wrapper">
                {properties.map((item, _idx) => {
                  return (
                    <SecondGuideCars key={_idx} image={item.thumbnail[0]} title={item.details.title} price={`${item.details.price_unit} ${item.details.price_amount}`}
                      bedrooms={item.details.bedrooms} bathrooms={item.details.bathrooms} area={item.details.size_construction} id={item.slug} />
                  )
                })}
              </div>
              :
              <Slider source={properties} type="props" secondGuideCardProp />
            }
          </div>
        </div>

        <div className="top-activities">
          <Title text="Top 10 Mallorca Activities" />
          <div className="wrapper-slider">
            <Slider source={activities} arrows thirdGuideCardProp />
          </div>
        </div>

        <Subscribe />

        <div className="mallorca-gallery-images">
          <Title text="Share the magic with over 600.000 followers <br/> on Instagram, Facebook and Tiktok" />
          <div className="gallery-images">
            <ElfsightWidget widgetID="1b26b4ce-7ba2-4fb8-8e72-fdcdfa618769" />
          </div>

        </div>

        <div className="mallorca-places-wrapper">

          <div className="mallorca-places">
            <div className="col-left">
              <Title text="Mallorca property gallery" />
              <div className="wrapper-destination">

                {/* <Dropdown handlePlacesChange={handlePlacesChange} handlePlacesQuery={handlePlacesQuery} placesQuery={placesQuery} values={areas} /> */}

                <div className="places-wrapper">
                  {
                    galleryProperties.map((property, _idx) => (
                      <Link key={`${property.slug}-${_idx}`} to={`/property/${property.slug}`} onMouseEnter={() => setActiveMarker(property.slug)} onMouseLeave={() => setActiveMarker("")}>
                        <div className="place">
                          <img src={property.thumbnail[0]} alt="destination" />
                          <div className="text">
                            <h3>{property.details.title}</h3>
                            <h2>{property.details.loc_city}</h2>
                            <p>{`${property.details.price_amount} ${property.details.price_unit}`}</p>
                          </div>
                        </div>
                      </Link>
                    ))
                  }
                </div>
              </div>
            </div>
            <div className="col-right">
              <PropertiesMap {...props} active={activeMarker} />
            </div>
          </div>
        </div>
      </DefaultLayout>
    </div>
  )
}
